.App{
/* border: 2px solid red;   */
background-color: black;
/* width: 100; */
height: 100vh;
width: 100vw;
display: flex;
flex-direction: column;
align-items: center;
justify-content: center;
}
.item-parent{
    /* border: 2px solid red; */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 45px 15px;
    width: 70vw;
    background-color: rgb(16, 255, 195);
    border-radius: 30px 30px 0px 0px;
    color: white;
    z-index: 99;
    box-shadow: 0px 2px 40px rgb(37, 37, 37);
}
.new-todo{
    /* border: 2px solid red; */
    width: 72vw;
    padding: 30px 0px;
    border-radius: 0px 0px 30px 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(181, 186, 187);
}
.del-button{
    padding: 10px 15px;
    border-radius: 5px;
    background: none;
    border: none;
}
.del-button:hover{
    scale: 1.1;
}
img{
    width: 30px;
}
.item{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: rgb(17, 194, 150);
    margin: 5px 0px;
    width: 70%;
    padding: 0px 20px;
    /* border: 2px solid red; */
}
input{
    padding: 15px 20px;
    color: black;
    width: 59%;
    border: none;
}
.add-button{
    padding: 15px;
    background-color: rgb(0, 255, 191);
    color: #2c2c2c;
    border: none;
}
.loader {
  width: 50px;
  padding: 8px;
  aspect-ratio: 1;
  border-radius: 50%;
  background: #25b09b;
  --_m: 
    conic-gradient(#0000 10%,#000),
    linear-gradient(#000 0 0) content-box;
  -webkit-mask: var(--_m);
          mask: var(--_m);
  -webkit-mask-composite: source-out;
          mask-composite: subtract;
  animation: l3 1s infinite linear;
}
@keyframes l3 {to{transform: rotate(1turn)}}

@media only screen and (max-width:650px){
    input{
    padding: 15px 5px;
    color: black;
    width: 50%;
    border: none;
}
.add-button{
    padding: 15px 10px;
    font-size: 13px;
    background-color: rgb(0, 255, 191);
    color: #2c2c2c;
    border: none;
}
}